import './public-path'
import Vue from 'vue'
import App from './App.vue'
import Router from './router'
import store from './store'
import QbUI from 'qb-ui'
import 'qb-ui/lib/theme-chalk/index.css'

Vue.config.productionTip = false
Vue.use(QbUI)

// let instance = null

function render (props = {}) {
  const { container } = props
  const router = Router(props)

  Vue.prototype.$closeNavTab = (path, route) => {
    if (props.closeNavTab) {
      return props.closeNavTab({
        id: path,
        to: route
      })
    }
    router.push(route)
  }

  // instance =
  new Vue({
    router,
    store,
    render: h => h(App)
  }).$mount(container ? container.querySelector('#app') : '#app')
}

if (!window.__POWERED_BY_QIANKUN__) {
  render()
}

export async function bootstrap () {
  console.log('sub2 bootstrapped')
}

export async function mount (props) {
  console.log('sub2 mounted')
  render(props)
}

export async function unmount () {
  // instance.$destroy()
  // instance.$el.innerHTML = ''
  // instance = null
}
